import { Component, OnInit } from '@angular/core';
import { DriverDetailsService } from 'src/services/driverDetails.service';
import { Globals } from 'src/services/common/globals';
import { Location } from '@angular/common';
import { Router } from '@angular/router';
@Component({
  selector: 'app-driver-details',
  templateUrl: './driver-details.component.html',
  styleUrls: ['./driver-details.component.scss']
})
export class DriverDetailsComponent implements OnInit {
  addDetail: boolean;
  loading: boolean = true;
  showImage: boolean;
  animal: string;
  name: string;
  staffID: number;
  staffReturnDetails: any;
  onLoad: boolean;
  dob: any;
  gender: string;
  fullAddress: any;
  approvedDate: any;
  customerID: any;
  staffReturnProfile: any;
  updateStaffProfile: any;
  staffAddressId: any;
  profileImage: any;
  driverLicenseFrontImage: any;
  driverLicenseBackImage: any;
  vehicleInsurance: any;
  vehicleRegistration: any;
  status: any;
  additionalDetails: { customer: { customerID: number; name: string; }; staff: { staffID: number; preferredCommunication: string; status: string; }; address: {}; };
  isRequestSent: boolean = false;
  img: any;
  approvePopup: boolean;
  declinedPopup: boolean;
  addressID: number;
  email: any;
  mobile: any;

  constructor(private driverDetailsService: DriverDetailsService, private location: Location, private global: Globals, private router: Router) { }

  ngOnInit(): void {
    if (!this.global.customerID) {
      this.goBack();
    }
    this.getStaffDetails();
  }

  AdditionalDetail() {
    this.addDetail = true;
  }

  popupClose(): void {
    this.addDetail = false;
  }

  getStaffDetails() {
    this.customerID = this.global.customerID;
    this.driverDetailsService.getStaffProfile(this.customerID).subscribe((res: any) => {
      this.loading = false;
      this.staffReturnProfile = res.data;
      this.profileImage = this.staffReturnProfile.profileImage;
      this.name = this.staffReturnProfile.name;
      this.dob = this.staffReturnProfile.dob;
      this.gender = this.staffReturnProfile.gender;
      this.email = this.staffReturnProfile.email;
      this.mobile = this.staffReturnProfile.mobile;
      this.fullAddress = this.staffReturnProfile.fullAddress;
      this.approvedDate = this.staffReturnProfile.submissionDate;
      this.profileImage = this.staffReturnProfile.profileImage;
      this.driverLicenseFrontImage = this.staffReturnProfile.driverLicenseFrontImage;
      this.driverLicenseBackImage = this.staffReturnProfile.driverLicenseBackImage;
      this.vehicleInsurance = this.staffReturnProfile.vehicleInsurance;
      this.vehicleRegistration = this.staffReturnProfile.vehicleRegistration;
      this.staffID = this.staffReturnProfile.staffID;
      this.status = this.staffReturnProfile.status;
      this.addressID = this.staffReturnProfile.addressID;
    }, (err) => {
      console.log('Error while getting staffProfile: ' + JSON.stringify(err));
    });
  }

  clear() {
    this.customerID = '';
  }

  goApproved() { }

  changeState(status: any) {
    let customer = {
      customerID: this.customerID,
      name: this.name
    }

    let staff = {
      customerID: this.customerID,
      staffID: this.staffID,
      status: status,
      staffAddressID: this.addressID
    }

    let staffVO = {
      customer,
      staff
    }
    this.driverDetailsService.updateStaffProfile(staffVO).subscribe((res: any) => {
      this.updateStaffProfile = res.data;
      if (status == 'APPROVED') {
        this.approvePopup = true;
        return;
      }
      if (status == 'DECLINED') {
        this.declinedPopup = true;
        return;
      }
    }, (err) => {
      console.log('Error while updating status: ' + JSON.stringify(err));
    });
  }
  Change(evt) {
    console.log(evt);
  }
  goBack() {
    this.router.navigate(['dashboard']);
  }
  showImgPopup(url) {
    if (url == null) {
      this.showImage = false;
      return;
    }
    this.showImage = true;
    this.img = url
  }
  hideImage() {
    this.showImage = false;
  }
  goToDashboard(status): void {
    this.approvePopup = false;
    this.declinedPopup = false;
    this.global.status = status;
    this.router.navigate(['dashboard']);
  }
}
