import { Component, OnInit } from '@angular/core';
import { DatePipe } from '@angular/common';
import { ReturnDetailsService } from 'src/services/returnDetails.service';


@Component({
  selector: 'app-pickup-details',
  templateUrl: './pickup-details.component.html',
  styleUrls: ['./pickup-details.component.scss']
})
export class PickupDetailsComponent implements OnInit {
  monthFlag: boolean;
  todayFlag: boolean;
  weekFlag: boolean;
  pickUpDetails: any;
  toDate: any;
  fromDate: any;
  loading: boolean = true;
  showImage: boolean = false;
  img: any;
  constructor(private returnDetailsService: ReturnDetailsService, private datePipe: DatePipe) { }

  ngOnInit(): void {
    this.clear();
    this.todayDetails();
  }
  todayDetails() {
    this.monthFlag = false;
    this.todayFlag = true;
    this.weekFlag = false;
    this.toDate = new Date();
    this.fromDate = new Date();
    this.getPickUpDetails();
    this.clear();
  }
  monthDetails() {
    this.monthFlag = true;
    this.todayFlag = false;
    this.weekFlag = false;
    let currentDay = new Date();
    this.toDate = new Date();
    this.fromDate = new Date(currentDay.getFullYear(), currentDay.getMonth(), 1);
    this.toDate = new Date(currentDay.getFullYear(), currentDay.getMonth() + 1, 0);
    this.getPickUpDetails();
    this.clear();
  }
  weekDetails() {
    this.monthFlag = false;
    this.todayFlag = false;
    this.weekFlag = true;
    let day = new Date().getDay();
    this.toDate = new Date();
    this.fromDate = new Date(new Date().getTime() - 60 * 60 * 24 * day * 1000);
    this.toDate = new Date(new Date().setDate(this.fromDate.getDate() + 7));
    this.getPickUpDetails();
    this.clear();
  }

  getPickUpDetails() {
    let returnDetailsVO = {
      fromDate: this.datePipe.transform(this.fromDate, 'yyyy-MM-dd'),
      toDate: this.datePipe.transform(this.toDate, 'yyyy-MM-dd')
    }
    this.returnDetailsService.getReturnDetails(returnDetailsVO).subscribe((res: any) => {
      this.loading = false;
      this.pickUpDetails = res.data;
    },
      (err) => {
        console.log("Error while getting return details: " + err);
      });
  }

  clear() {
    this.fromDate = '';
    this.toDate = '';
  }
  reset() {
    this.fromDate = '';
    this.toDate = '';
    this.todayDetails();
  }

  showImg(url) {
    this.showImage = true;
    this.img = url;
  }

  hideImage() {
    this.showImage = false;
  }
}
