import { Component, OnInit } from '@angular/core';
import { DriverDetailsService } from 'src/services/driverDetails.service';
import { Globals } from 'src/services/common/globals';
import { Router } from '@angular/router';
import { StaffTrackingService } from '../services/common/staff.service';

@Component({
  selector: 'app-additional-details',
  templateUrl: './additional-details.component.html',
  styleUrls: ['./additional-details.component.scss']
})
export class AdditionalDetailsComponent implements OnInit {
  successPopup: boolean;
  customerID: number;
  profileImage: any;
  staffReturnProfile: any;
  driverLicenseFrontImage: any;
  driverLicenseBackImage: any;
  vehicleInsurance: any;
  vehicleRegistration: any;
  staffID: any;
  status: any;
  name: any;
  requestedDetails: any = [];

  constructor(private driverDetailsService: DriverDetailsService, private global: Globals, private staffTrackingService: StaffTrackingService, private router: Router) { }

  ngOnInit(): void {
    if (!this.global.customerID) {
      this.goBack();
    }
    this.getStaffDetails();
  }

  getStaffDetails() {
    this.customerID = this.global.customerID;
    this.driverDetailsService.getStaffProfile(this.customerID).subscribe((res: any) => {
      this.staffReturnProfile = res.data;
      this.name = this.staffReturnProfile.name;
      this.profileImage = this.staffReturnProfile.profileImage;
      this.driverLicenseFrontImage = this.staffReturnProfile.driverLicenseFrontImage;
      this.driverLicenseBackImage = this.staffReturnProfile.driverLicenseBackImage;
      this.vehicleInsurance = this.staffReturnProfile.vehicleInsurance;
      this.vehicleRegistration = this.staffReturnProfile.vehicleRegistration;
      this.staffID = this.staffReturnProfile.staffID;
      this.status = this.staffReturnProfile.status;
    }, (err) => {
      console.log('Error while getting staffProfile: ' + JSON.stringify(err));
    });
  }

  requestedChanges(event, value) {
    if (event.checked)
      this.requestedDetails.push(value);
    if (!event.checked) {
      const index = this.requestedDetails.indexOf(value);
      if (index > -1)
        this.requestedDetails.splice(index, 1);
    }
  }

  submit() {
    let additionalDetails = {
      "customer": {
        "customerID": this.customerID,
        "name": this.name
      },
      "staff": {
        "staffID": this.staffID,
        "status": "ADDITIONAL_INFO_REQUESTED"
      },
      "address": {},
      "requestedDetails": this.requestedDetails
    }
    this.staffTrackingService.updateStaffProfile(additionalDetails).subscribe((sendUserResponse: any) => {
      this.successPopup = true;
    },
      (err) => {
        console.log("ERROR");
      });
  }

  closePopup() {
    this.successPopup = false;
    this.router.navigate(['dashboard']);
  }

  goBack() {
    this.router.navigate(['dashboard']);
  }

}
