import { Injectable } from '@angular/core';
import { BaseService } from './base.service';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class DriverDetailsService extends BaseService {


  getStaffProfile(customerID: number): Observable<any> {
    const req = {
      url: `/get-staff-profile/${customerID}`,
      name: 'GetStaffProfile',
    };
    return this.get(req);
  }

  updateStaffProfile(staffDetailVO:any) {
    const req = {
      url: `/update-staff-profile`,
      name: 'updateStaffProfile',
      body: staffDetailVO
    }
    return this.put(req);
  }

  
}
