import { environment as ENV } from '../environments/environment';
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { map } from "rxjs/operators";
import { Globals } from './common/globals';

@Injectable({
    providedIn: 'root'
})
export class BaseService {
    API_URL: string = ENV.API_URL;

    constructor(protected http: HttpClient,
        protected globals: Globals) { }

    get(req: any) {
        if (!req || !req.url) {
            throw 'Request cannot be null';
        }

        req.url = req.url.startsWith('http') ? req.url : this.API_URL + req.url;

        //TODO: handle timeout scenario
        const res = this.http.get(req.url).pipe(map((response: Response) => {
            console.log(`${req.name} is retrived from server`);
            return response;
        }));
        return res;
    }

    put(req: any) {
        if (!req || !req.url) {
            throw 'Request cannot be null';
        }
        req.url = this.API_URL + req.url;
        const res = this.http.put(req.url, req.body).pipe(map((response: Response) => {
            console.log(`${req.name} is updated in the server`);
            return response;
        }));
        return res;
    }

    post(req: any) {
        if (!req || !req.url) {
            throw 'Request cannot be null';
        }
        req.url = this.API_URL + req.url;

        const res = this.http.post(req.url, req.body);

        return res;
    }
}
