import { Injectable } from '@angular/core';
import { BaseService } from './base.service';

@Injectable({
  providedIn: 'root'
})

export class SignupService extends BaseService {


  sendOTP(input: any, applicationType: string): any {
    const req = {
      url: `/send-otp/${applicationType}`,
      name: 'SendOTP',
      body: input
    };
    return this.post(req);
  }
  verifyUserLogin(input: any): any {
    const req = {
      url: `/login`,
      name: 'VerifyUserLogin',
      body: input
    };
    return this.post(req);
  }
  verifyOTP(input: any): any {
    const req = {
      url: `/verify-otp`,
      name: 'VerifyOTP',
      body: input
    };
    return this.post(req);
  }

}
