import { Component, OnInit } from '@angular/core';

import { Globals } from 'src/services/common/globals';
import { Router } from '@angular/router';
import { StaffTrackingService } from '../services/common/staff.service';

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.scss']
})
export class DashboardComponent implements OnInit {
  status: any;
  loading: boolean = true;
  staffDetailsReturnRequest: any;
  staffDetail: any;
  name: any;
  staffSearch: any;
  isChecked: any;

  transform(staffDetailsReturnRequest: any[], filterVAlue: string): any {
    return staffDetailsReturnRequest.filter(i => i.name.includes(filterVAlue))
  }
  constructor(private staffTrackingService: StaffTrackingService, private global: Globals, private router: Router) { }

  ngOnInit(): void {

    if (this.global.status == null) {
      this.status = 'SUBMITTED';
      this.getStaff(this.status);
    } else {
      this.status = this.global.status;
      this.status === 'ALL' ? this.getStaffAllDetailsService() : this.getStaffDetailsService();
    }
  }
  getStaff(event) {
    event === 'SUBMITTED' ? this.status = 'SUBMITTED' : this.status = (event.target as HTMLInputElement).value;
    this.status === 'ALL' ? this.getStaffAllDetailsService() : this.getStaffDetailsService();
  }
  getStaffDetailsService() {
    this.staffTrackingService.getStaffDetails(this.status).subscribe((res: any) => {
      if (res) {
        this.loading = false;
        this.staffDetailsReturnRequest = res.data;
      }
    }, (err) => { console.log(`Error while getting staff details" : ${err}`); });
  }
  getStaffAllDetailsService() {
    this.staffTrackingService.getAllStaffDetails().subscribe((res: any) => {
      if (res) {
        this.loading = false;
        this.staffDetailsReturnRequest = res.data;
      }
    }, (err) => { console.log(`Error while getting all staff details" : ${err}`); });
  }
  getStaffDetail(customer) {
    this.global.customerID = customer.customerID;
    this.global.status = this.status;
    this.router.navigate(['driver-details']);
  }
  clearSearchText(): void {
    this.staffSearch = '';
  }
}
