import { Injectable } from '@angular/core';
import { BaseService } from '../base.service';


@Injectable({
  providedIn: 'root'
})

export class StaffTrackingService extends BaseService {

  getStaffDetails(status: any): any {
    const req = {
      url: `/get-staff/${status}`,
      name: 'getstaffdetails'
    };
    return this.get(req);

  }
 
  updateStaffProfile(staffProfile: any): any {
    const req = {
      url: '/update-staff-profile',
      name: 'updateStaffProfile',
      body: staffProfile
    };
    return this.put(req);
  }

  getAllStaffDetails() {
    const req = {
      url: `/get-all-staff`,
      name: 'getallstaffdetails'
    };
    return this.get(req);
  }
}
