import { Component, ViewChild } from '@angular/core';
import { MatDrawer } from '@angular/material/sidenav';
import { ActivationEnd, NavigationEnd, Router } from '@angular/router';
import { Globals } from 'src/services/common/globals';

import { CacheService } from '../services/common/cache.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent {
  @ViewChild('drawer') drawer: MatDrawer;
  hideNavBar: boolean;
  popup: boolean;
  logoutmenuModals: boolean;
  pageTitle: any;
  constructor(private router: Router, private cacheService: CacheService, private globals: Globals) {
    router.events.subscribe((val) => {
      if (val instanceof NavigationEnd) {
        if (val.url === '/login' || val.url === '/' || val.url.includes('/login?')) {
          this.hideNavBar = true;
        }
        else {
          this.hideNavBar = false;
        }
      }
      if (val instanceof ActivationEnd)
        this.pageTitle = val.snapshot.data.title;
    });
  }

  clearStatus() {
    this.globals.status = null;
  }

  logout(): void {
    this.clearStatus();
    this.popup = false;
    this.logoutmenuModals = false;
    this.cacheService.clearUserDetail();
    this.router.navigate(['login']);
  }

  logoutmodal(): void {
    this.popup = true;
  }

  popupClose(): void {
    this.popup = false;
    this.logoutmenuModals = false;
  }

  logodashboard() {
    this.router.navigate(['dashboard']);
  }

  driverRequest(): void {
    this.drawer.toggle();
    this.router.navigate(['dashboard']);
  }

  pickupDetails(): void {
    this.drawer.toggle();
    this.router.navigate(['pickup-details']);
  }

  logoutMenu(): void {
    this.drawer.toggle();
    this.logoutmenuModals = true;
  }

}
