import { NgModule } from '@angular/core';
import { PreloadAllModules, RouterModule, Routes } from '@angular/router';
import { AuthGuard } from '../services/auth.guard';
import { AdditionalDetailsComponent } from './additional-details/additional-details.component';
import { DashboardComponent } from './dashboard/dashboard.component';
import { DriverDetailsComponent } from './driver-details/driver-details.component';
import { LoginComponent } from './login/login.component';
import { PickupDetailsComponent } from './pickup-details/pickup-details.component';

const routes: Routes = [
  { path: "", component: LoginComponent },
  { path: 'login', component: LoginComponent },
  { path: 'dashboard', component: DashboardComponent, canActivate: [AuthGuard], data: { title: "Driver Requests" } },
  { path: 'driver-details', component: DriverDetailsComponent, canActivate: [AuthGuard], data: { title: "Driver details" } },
  { path: 'pickup-details', component: PickupDetailsComponent, canActivate: [AuthGuard], data: { title: "Pickup details" } },
  { path: 'additional-details', component: AdditionalDetailsComponent, canActivate: [AuthGuard], data: { title: "Request for additional details" } },
];

@NgModule({
  imports: [RouterModule.forRoot(routes, { preloadingStrategy: PreloadAllModules })],
  exports: [RouterModule]
})
export class AppRoutingModule { }
